<template>
  <layout>
    <page-header :title="title"></page-header>
    <div>
      <b-card
        class="mb-2 text-center"
        title="Egreso de inventario"
      >
        <b-card-text>
          <template v-if="loadingProducts">
            <b-skeleton animation="wave" width="85%"></b-skeleton>
            <b-skeleton animation="wave" width="55%"></b-skeleton>
            <b-skeleton animation="wave" width="70%"></b-skeleton>
            <b-skeleton animation="wave" width="55%"></b-skeleton>
            <b-skeleton animation="wave" width="85%"></b-skeleton>
          </template>
          <template v-else>
            <div 
              class="d-grid gap-2 d-md-flex justify-content-md-end"
            >
              <div class="form-check form-switch form-switch-md mb-3 form-check-reverse">
                <label 
                  class="form-check-label custom-label" 
                  for="flexSwitchCheckDefault"
                >
                  Única unidad
                </label>
                <input 
                  class="form-check-input" 
                  type="checkbox" 
                  role="switch" 
                  id="flexSwitchCheckDefault"
                  v-model="uniqueProduct"
                  :disabled="checkGuideLocal"
                >
              </div>
            </div>
            <div class="d-grid gap-2 d-md-flex justify-content-md-end">
              <div class="form-check form-switch form-switch-md mb-3 form-check-reverse">
                <label 
                  class="form-check-label custom-label" 
                  for="flexSwitchCheckDefault"
                >
                  Guía local
                </label>
                <input 
                  class="form-check-input" 
                  type="checkbox" 
                  role="switch" 
                  id="flexSwitchCheckDefault"
                  v-model="checkGuideLocal"
                  :disabled="uniqueProduct"
                >
              </div>
            </div>
            <b-form-group
            class="mb-2"
            v-show="auxShowItems"
            >
              <b-input-group>
                <b-form-input
                  id="input-1"
                  v-model="searchSku"
                  type="text"
                  placeholder="SKU"
                  required
                  autofocus
                  @keyup.enter="searchSku ? addProduct(): null"
                  ref="inputSku"
                ></b-form-input>
              </b-input-group>
            </b-form-group>
            <b-form-group
            >
              <b-input-group>
                <b-form-input
                  id="input-1"
                  v-model="packageGuide"
                  type="text"
                  placeholder="Ingrese o escanee el número de la guia"
                  required
                  @keyup.enter="packageGuide ? createExpenseAux() : null"
                  ref="inputGuide"
                ></b-form-input>
              </b-input-group>
            </b-form-group>
            <div v-show="productSelected.length">
              <b-table 
                striped 
                hover 
                :items="productSelected" 
                :fields="fields"
              >
                <template v-slot:cell(actions)="{ item }" v-if="!checkGuideLocal">
                  <b-btn @click="deleteRecord(item.sku)" variant="danger">
                    <i class="mdi mdi-delete"></i>
                  </b-btn>
                </template>
              </b-table>
              <!-- <div class="d-grid gap-2 d-md-flex justify-content-md-end">
                <b-btn
                  variant="success"
                  @click="createExpense()"
                  :disabled="packageGuide === null || (loadingCreate && submitCreate)"
                >
                  <template v-if="loadingCreate">
                    <b-spinner small type="grow"></b-spinner>
                    Cargando...
                  </template>
                  <template v-else>
                    Finalizar
                  </template>
                </b-btn>
              </div> -->
            </div>
            <div class="mt-2" v-show="!productSelected.length">
              <img class="img-fluid" src="@/assets/images/barcode.png" alt="..." width="300px">
            </div>
          </template>
        </b-card-text>
      </b-card>
      <b-card
        title="Últimos despachos"
        class="text-center"
      >
        <b-card-text>
          <template v-if="loadingDispatchs">
            <b-skeleton-table
                :rows="4"
                :columns="4"
                :table-props="{ bordered: true, striped: true }"
              ></b-skeleton-table>
          </template>
          <template v-else>
            <b-table 
                striped 
                hover 
                :items="lastDispatchs" 
                :fields="fieldsDispatch"
              >
              </b-table>
          </template>
        </b-card-text>
      </b-card>
    </div>
  </layout>
</template>
<script>
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import Swal from 'sweetalert2'
import { toast } from '@/utils/alerts'
import { decode_bs64 } from '@/utils/general'
import { ALL_PRODUCTS_CURRENT_STORE_USER } from '@/api/graphql/query/products'
import { CREATE_EXPENSE } from '@/api/graphql/mutations/expense'
import { LIST_DISPATCHS } from '@/api/graphql/query/dispatchs'
import { GET_GUIDE } from '@/api/graphql/query/guideLocal'
import { formatDate } from '@/utils/general'
import { storeComputed } from '@/state/helpers'

export default {
  name: 'ExpenseInventory',
  components: {
    Layout,
    PageHeader
  },
  data(){
    return{
      title: 'Inventario',
      loadingProducts: true,
      loadingDispatchs: true,
      lastDispatchs: [],
      searchSku: null,
      packageGuide: null,
      lastCursor: null,
      fieldsDispatch: [
        {
          key: "guide",
          label: "Guía",
        },
        {
          key: "store",
          label: "Tienda",
        },
        {
          key: "products",
          label: "Productos",
        },
        {
          key: "created",
          label: "Fecha despacho",
        }
      ],
      fields: [
        {
          key: 'name',
          label: 'Producto'
        },
        {
          key: 'quantity',
          label: 'Cantidad'
        },
        {
          key: 'actions',
          label: 'Acciones'
        }
      ],
      allProducts: [],
      productSelected: [],
      loadingCreate: false,
      submitCreate: false,
      uniqueProduct: false,
      checkGuideLocal: false
    }
  },
  watch: {
    uniqueProduct: function(newValue){
      this.$refs.inputSku.focus()
    },
    checkGuideLocal: function(newValue){
      this.productSelected = []
      this.packageGuide = null
      if(newValue === true){
        this.$refs.inputGuide.focus()
      }else{
        this.$nextTick(() =>{
          this.$refs.inputSku.focus()
        })
      }
    }
  },
  computed: {
    ...storeComputed,
    auxShowItems(){
      if(this.uniqueProduct === false && this.checkGuideLocal === false){
        return true
      }
      if (this.uniqueProduct === true && this.checkGuideLocal === false){
        return true
      }
      if(this.uniqueProduct === false && this.checkGuideLocal === true){
        return false
      }
      return false
    }
  },
  methods: {
    async getProducts(){
      try { 
        const response = await this.$apollo.query({
          query: ALL_PRODUCTS_CURRENT_STORE_USER,
          variables: {
            cursor: this.lastCursor,
          }
        })
        const { listProducts } = response.data
        if (listProducts !== null){
          const data = listProducts.edges
          this.hasNextPage = listProducts.pageInfo.hasNextPage
          this.addArrayProducts(data)
          if (this.hasNextPage){
            this.getProducts()
          }else{
            this.loadingProducts = false
          }
        }else{
          toast('error', 'No se pudo recuperar la información')
          this.loadingProducts = false
        }
      } catch (error) {
        this.loadingProducts = false
        toast('error', 'No se pudo recuperar la información')
      }
    },
    async getDispatchs(){
      try {
        const response = await this.$apollo.query({
          query: LIST_DISPATCHS,
          variables: {
            includeStore: true,
            includeSku: false,
            first: 4
          },
        });
        const { dispatchs } = response.data;
        if (dispatchs !== null) {
          const data = dispatchs.edges;
          this.hasNextPage = dispatchs.pageInfo.hasNextPage;
          this.formatTableLastDispatch(data)
        } else {
          toast("error", "No se pudo recuperar la información de los ultimos despachos");
        }
        this.loadingDispatchs = false
      } catch (error) {
        this.loadingDispatchs = false
        toast("error", "No se pudo recuperar la información de los ultimos despachos");
      }
    },
    addArrayProducts(data){
      for (let product of data){
        const { node } = product
        const temp = {
          id: node.id,
          name: node.name,
          price: node.price,
          stock: node.stock,
          sku: node.sku
        }
        this.allProducts.push(temp)
        this.lastCursor = product.cursor
      }
    },
    addProduct(){
      const index =   this.allProducts.findIndex( product => product.sku === this.searchSku)
      if (index === -1){
        toast('error', 'El producto no se ha encontrado')
      }else{
        if(this.uniqueProduct === true){
          this.$refs.inputGuide.focus()
        }
        const indexs = this.productSelected.findIndex( product => product.sku === this.searchSku)
        if (indexs !== -1){
          const product = this.productSelected[indexs]
          product.quantity += 1
        }else{
          this.productSelected.push(this.formatDataTable(this.allProducts[index]))
        }
      }
      this.searchSku = null
    },
    formatDataTable(data){
      const idInt = decode_bs64( data.id ).split(':')[1]
      const temp = {
        name: data.name,
        quantity: 1,
        sku: data.sku,
        id: idInt
      }
      return temp
    },
    formatDataMutation(products){
      const dataMutation = []
      for (let product of products){
        const temp = {
          quantity: product.quantity,
          product: product.id
        }
        dataMutation.push(temp)
      }
      return dataMutation
    },
    async createExpense(){
      this.loadingCreate = true
      this.submitCreate = true
      const dataExpense = this.formatDataMutation(this.productSelected)
      try {
        const response = await this.$apollo.mutate({
          mutation: CREATE_EXPENSE,
          variables: {
            expense: dataExpense,
            packageGuide: this.packageGuide
          }
        })
        const { createExpense } = response.data
        if(createExpense !== null){
          const { expense } = createExpense
          if (expense === true){
            this.addNewExpenseToLastDispatchs()
            this.productSelected = []
            this.packageGuide = null
            if(this.checkGuideLocal === true){
              this.$refs.inputGuide.focus()
            }else{
              this.$refs.inputSku.focus()
            }
            toast('success', 'El egreso se realizo correctamente')
          }else{
            toast('error', 'El egreso no se pudo realizar')
          }
        }else{
          toast('error', 'El egreso no se pudo realizar')
        }
        this.loadingCreate = false
        this.submitCreate = false
      } catch (error) {
        this.loadingCreate = false
        this.submitCreate = false
        toast('error', 'El egreso no se pudo realizar')
      }
    },
    async createExpenseAux(){
      if (this.checkGuideLocal === true){
        await this.getGuideLocal()
      }
      if (this.productSelected.length === 0){
        return toast('error', `No hay productos seleccionados para la guía ${this.packageGuide}`)
      }
      let textHtml = `<p> Estas seguro de guardar el egreso con número de guía <strong>${this.packageGuide}</strong></p> `
      Swal.fire({
        title: '¿Estas seguro?',
        html: textHtml,
        icon: 'question',
        showCancelButton: true,
        cancelButtonText: 'No, cancelar',
        cancelButtonColor: '#bb2d3b',
        confirmButtonText: 'Si, guardar',
        showLoaderOnConfirm: true,
        preConfirm: () => {
          return this.createExpense()
        }
      })
    },
    deleteRecord(sku){
      const index = this.productSelected.findIndex(product => product.sku === sku)
      if (index !== -1){
        this.productSelected.splice(index, 1)
      }
    },
    formatTableLastDispatch(data){
      for(let item of data){
        const temp = {
          guide: item.node.guide,
          store: item.node.store.name,
          created: formatDate(item.node.created),
          products: this.sumQuantityProducts(
            item.node.productpackagethroughSet
          ),
        }
        this.lastDispatchs.push(temp)
      }
    },
    sumQuantityProducts(products) {
      let count = 0;
      for (let item of products) {
        count += item.quantity;
      }
      return count;
    },
    async addNewExpenseToLastDispatchs(){
      this.lastDispatchs.pop()
      const temp = {
        'guide': this.packageGuide,
        'store': this.currentStoreUser,
        'created': formatDate(new Date()),
        'products': this.productSelected.reduce((a, b) => a + (b.quantity || 0), 0)
      }
      this.lastDispatchs.unshift(temp)
    },
    async getGuideLocal(){
      try {
        const response = await this.$apollo.query({
          query: GET_GUIDE,
          variables: {
            guideNumber: this.packageGuide,
          }
        })
        const { guidesLocal } = response.data
        if (guidesLocal === null){
          return toast('error', 'No se pudo recuperar la información')
        }
        const data = guidesLocal.edges[0].node.guidelocalproductthroughSet.edges
        this.productSelected = this.formatDataGuideLocalToAddTable(data)
        return true
      } catch (error) {
        console.log(error)
        return toast('error', 'Error al recuperar la información')
      }
    },
    formatDataGuideLocalToAddTable(data){
      const temp = []
      for (let item of data){
        const tempObject = {
          name: item.node.product.name,
          quantity: item.node.quantity,
          sku: item.node.product.sku,
          id: decode_bs64( item.node.product.id ).split(':')[1]
        }
        temp.push(tempObject)
      }
      return temp
    }
  },
  beforeMount(){
    this.getProducts()
    this.getDispatchs()
  }
}
</script>
<style scoped>
.custom-label{
  margin-right: 5px;
}
</style>