import gql from 'graphql-tag'

const LIST_DISPATCHS = gql`query allDispatchs($store: String, $guide: String,
    $cursor: String, $includeStore: Boolean!, $includeSku: Boolean!, $startDate: DateTime,
    $endDate: DateTime, $first: Int){
    dispatchs(store_Name_Iexact: $store, guide_Icontains: $guide after: $cursor,
        created_Date_Gte: $startDate, created_Date_Lte: $endDate, first: $first){
        pageInfo{
            hasNextPage
        }
        edges{
            cursor
            node{
                guide
                created
                productpackagethroughSet{
                    product{
                        name
                        sku @include(if: $includeSku)
                        id
                    }
                    quantity
                }
                store @include(if: $includeStore){
                    name
                }
            }
        }
    }
}`

const DISPATCHS_RETURNS_BY_STORE = gql`query DispatchsReturns($store: Int, $startDate: Date!, $endDate: Date!){
    returnsDispatchs(startDate: $startDate, endDate: $endDate, store: $store){
        dispatchs
        returns
        storeName
        storeId
    }
}`

export {
    LIST_DISPATCHS,
    DISPATCHS_RETURNS_BY_STORE
}