import gql from 'graphql-tag'

const ALL_PRODUCTS_CURRENT_STORE_USER = gql`query listProducts($cursor: String, $productName: String){
    listProducts(after: $cursor, name_Icontains: $productName){
        pageInfo{
            hasNextPage
        }
        edges{
            cursor
            node{
                name
                id
                stock
                price
                sku
                store{
                    name
                }
            }
        }
    }
}`

const PRODUCTS_LOW_STOCK = gql`query productsLowStock($store: Int){
    productsLowStock(store: $store){
        stock
        product
        store
        id
    }
}`

export {
    ALL_PRODUCTS_CURRENT_STORE_USER,
    PRODUCTS_LOW_STOCK
}